#custom-header {
    padding: 0px 150px !important;
}

.custom-searchbox {
    height: 35px !important;
    width: 300px !important;
}

#custom-mobile-menu {
    display: none !important;
}

#main-content-wrapper {
    margin-top: 50px;
    padding: 0 150px;
}

#main-content {
    margin-left: 32px;
}

.ant-layout-sider-light {
    background: transparent !important;
}

.sidebar-icon {
    font-size: 20px !important;
}

.sidebar-mobile-items, .sidebar-items {
    cursor: pointer;
    border-radius: 5px !important;
}

.sidebar-items {
    padding: 10px;
}

.sidebar-mobile-items {
    /* padding: 15px 0px; */
}

.sidebar-items-active, .sidebar-items:hover {
    background: #fff;
    font-weight: bold;
    border: solid 1px #bdc3c7;
}

.custom-searchbox-mobile {
    margin-bottom: 15px;
}

.custom-sidebar-divider {
    margin: 15px 0px !important;
}

@media only screen and (max-width: 768px) {
    #custom-header {
        padding: 0px 30px !important;
    }

    #custom-header .ant-input-search, .ant-layout-sider-light {
        display: none !important;
    }

    #custom-desktop-menu {
        display: none !important;
    }
    
    #custom-mobile-menu {
        display: block !important;
    }

    #main-content-wrapper {
        margin-top: 25px !important;
        padding: 0 30px !important;
    }

    #main-content {
        margin-left: 0 !important;
    }
}